import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons';

import './style.scss';

export class Pagination extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="paginationSection">
                <div className="prevPage" onClick={(e) => { this.props.paginationCall("prev") }}>
                    <i><FontAwesomeIcon icon={faChevronLeft} /></i>
                </div>
                <div className="currentPage">
                    <p>Page No: {this.props.pgNumber + 1}</p>
                </div>
                <div className="nextPage" onClick={(e) => { this.props.paginationCall("next") }}>
                    <i><FontAwesomeIcon icon={faChevronRight} /></i></div>
            </div>
        )
    }
}

export default Pagination;

