import React, { Component } from 'react';
import Thumbnail from "./productthumbnail/index";
import Pagination from "../pagination/index";
import Aside from "./aside/index";
import "./style.scss";

class Home extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            products: [],
            loaded: false,
            pageNumber: 0,
            pagiBack: 0,
            orderParam: "date",
            orderBy: "asc"
        }
        this.paginationCall = this.paginationCall.bind(this);
    }

    componentDidMount() {
        const url = "http://mocsyamtestapi-env.eba-dvawv2zg.ap-south-1.elasticbeanstalk.com/products/?pageNumber=0&OrderParam=" + this.state.orderParam + "&OrderBy=" + this.state.orderBy;
        fetch(url)
            .then(res => {
                return res.json()
            })
            .then(
                (result) => {
                    console.log(result)
                    this.setState((prevState) => ({ products: result, loaded: true }), () => { console.log("done") })
                },
                (error) => {
                    console.log(error)
                }
            )
        // this.getData();
    }

    paginationAPI = () => {
        const url = "http://mocsyamtestapi-env.eba-dvawv2zg.ap-south-1.elasticbeanstalk.com/products/?pageNumber=" + this.state.pageNumber + "&OrderParam=" + this.state.orderParam + "&OrderBy=" + this.state.orderBy;

        this.setState((prevState) => ({ loaded: false }), () => {
            fetch(url)
                .then(res => {
                    return res.json()
                })
                .then(
                    (result) => {
                        if (result.errorMessage) {
                            this.setState((prevState) => ({ pageNumber: prevState.pagiBack, loaded: true }), () => {
                                alert("No more Products.")
                            })
                        } else {
                            this.setState((prevState) => ({ products: result, loaded: true }), () => { console.log("done") })
                        }
                    },
                    (error) => {
                        console.log(error)
                    }
                )
        })
    }

    paginationCall = (np) => {
        if (np == "next") {
            this.setState((prevState) => ({ pageNumber: (prevState.pageNumber + 1), pagiBack: prevState.pageNumber }), () => { this.paginationAPI() })
        } else {
            if (!(this.state.pageNumber == 0)) {
                this.setState((prevState) => ({ pageNumber: (prevState.pageNumber - 1), pagiBack: prevState.pageNumber }), () => { this.paginationAPI() })
            }
        }
    }

    SortingAPI = () => {
        const url = "http://mocsyamtestapi-env.eba-dvawv2zg.ap-south-1.elasticbeanstalk.com/products/?pageNumber=0" + "&OrderParam=" + this.state.orderParam + "&OrderBy=" + this.state.orderBy;
        this.setState((prevState) => ({ loaded: false }), () => {
            fetch(url)
                .then(res => {
                    return res.json()
                })
                .then(
                    (result) => {
                        if (result.errorMessage) {
                            this.setState((prevState) => ({ pageNumber: prevState.pagiBack, loaded: true }), () => {
                                alert("No more Products.")
                            })
                        } else {
                            this.setState((prevState) => ({ products: result, loaded: true, pageNumber: 0 }), () => { console.log("done") })
                        }
                    },
                    (error) => {
                        console.log(error)
                    }
                )
        })
    }

    sorting = (type) => {
        if (type == "pricelh") {
            this.setState((prevState) => ({ orderParam: "price", orderBy: "asc" }), () => { this.SortingAPI() })
        } else if (type == "pricehl") {
            this.setState((prevState) => ({ orderParam: "price", orderBy: "desc" }), () => { this.SortingAPI() })
        } else if (type == "latest") {
            this.setState((prevState) => ({ orderParam: "date", orderBy: "desc" }), () => { this.SortingAPI() })
        } else {
            this.setState((prevState) => ({ orderParam: "date", orderBy: "asc" }), () => { this.SortingAPI() })
        }
    }
    render() {
        return (
            <React.Fragment>
                {this.state.loaded ? (
                    <section className="homePage">
                        <Aside sortingCB={this.sorting} />
                        <div className="productionSection">
                            {this.state.products.map(item => {
                                return (
                                    <Thumbnail title={item.title} description={item.description} ThumbnailImage={item.productImage[0].imageUrl} id={item.id} price={item.price} originalPrice={item.originalPrice} discount={item.discountPercent} />)
                            })}
                        </div>
                        <Pagination paginationCall={this.paginationCall} pgNumber={this.state.pageNumber} />
                    </section>
                ) : (
                    <div id="preloader">
                        <div class="preloader-container">
                            <div class="preloader-animation"></div>
                        </div>
                    </div>
                )
                }
            </React.Fragment>
        )
    }

}

export default Home;