import React, { Component } from 'react'

import './style.scss'

export class Aside extends Component {
    constructor(props) {
        super(props)
    }



    render() {
        return (
            <div className="sortFilter">
                <ul className="sortList">
                    Sort by:
                    <li><input type="radio" name="sorting" id="priceLH" value="priceLH" /><label onClick={() => { this.props.sortingCB("pricelh") }} htmlFor="priceLH">price low-to-high</label></li>
                    <li><input type="radio" name="sorting" id="priceHL" value="priceHL" /><label htmlFor="priceHL" onClick={() => { this.props.sortingCB("pricehl") }}>price high-to-low</label></li>
                    <li><input type="radio" name="sorting" id="latest" value="latest" /><label onClick={() => { this.props.sortingCB("latest") }} htmlFor="latest">Latest</label></li>
                    <li><input type="radio" name="sorting" id="oldest" value="oldest" /><label onClick={() => { this.props.sortingCB("oldest") }} htmlFor="oldest">Oldest</label></li>
                </ul>
                <ul className="filterList">
                    Filter by:
                    <li><input type="checkbox" name="outOfStock" id="outOfStock" value="outOfStock" onChange={() => { }} /><label htmlFor="outOfStock">Exclude out of Stock</label></li>
                    <li><input type="checkbox" name="isExpress" id="isExpress" value="isExpress" onChange={() => { }} /><label htmlFor="isExpress" >Is express delivery</label></li>
                    <li class="minMax">
                        <ul>
                            Price:
                            <li>
                                <label htmlFor="">Min.</label>
                                <input type="number" />    </li>
                            <li>
                                <label htmlFor="">Max.</label>
                                <input type="number" />    </li>
                            <li><button className="applyBtn">Apply</button></li>
                        </ul>
                    </li>
                </ul>
            </div>
        )
    }
}

export default Aside
