import React, { Component } from 'react'
import { withRouter } from 'react-router';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faRupeeSign } from '@fortawesome/free-solid-svg-icons';

import './style.scss'

export class Product extends Component {
    constructor(props) {
        super(props)
        this.state = { id: "", prod: {}, loaded: false };
    }

    componentDidMount() {
        this.setState((prevState) => ({ id: this.props.match.params.id }), () => {
            fetch("http://mocsyamtestapi-env.eba-dvawv2zg.ap-south-1.elasticbeanstalk.com/products/" + this.state.id)
                .then((res) => {
                    return res.json()
                })
                .then((result) => {
                    console.log(result)
                    this.setState((prevState) => ({ prod: result, images: result.productImage, loaded: true }))
                })
        })
    }

    changeImage = (index) => {
        console.log(this.state.images[index])
        document.getElementById("mainPic").setAttribute("src", this.state.images[index].imageUrl);
    }

    render() {
        return (
            <React.Fragment>
                {this.state.loaded ? (
                    <section className="prodpage">
                        <div className="imageContainer">
                            <div className="imageList">
                                {this.state.images.map((item, index) => {
                                    return <img src={item.imageUrl} alt="" onClick={() => { this.changeImage(index) }} />
                                })}
                            </div>
                            <div className="selectedImage">
                                <img id="mainPic" src={this.state.images[0].imageUrl} alt="" />
                            </div>
                        </div>
                        <div className="prodInfo">
                            <h2 className="prodTitle">{this.state.prod.title}</h2>
                            <p className="prodDesc">{this.state.prod.description}</p>
                            <div className="pricing">
                                <div className="presentPrice"><i><FontAwesomeIcon icon={faRupeeSign} /></i>{this.state.prod.price}</div>
                                <div className="originalPrice"><i><FontAwesomeIcon icon={faRupeeSign} /></i>{this.state.prod.originalPrice}</div>
                                <div className="discount">{this.state.prod.discountPercent}% off</div>
                            </div>
                        </div>
                    </section>
                ) : (
                    <div id="preloader">
                        <div class="preloader-container">
                            <div class="preloader-animation"></div>
                        </div>
                    </div>
                )
                }
            </React.Fragment>
        )
    }
}

export default withRouter(Product)
