import React from 'react';
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faRupeeSign } from '@fortawesome/free-solid-svg-icons';

import Wishlist from './wishlist/index';
import Addcartquantity from './addcartquantity/index';
import "./style.scss"

class Thumbnail extends React.Component {
    constructor(props) {
        super(props);
        this.state = { loaded: false, quantity: 0 }
        this.productQuantity = this.productQuantity.bind(this);
    }

    productQuantity(count, price) {
        this.props.productQuantity(count, price)
    }

    render() {
        return (
            <div className="navLink">
                <div className="productChild">
                    <Wishlist />
                    <Link to={"/product/" + this.props.id} className="navLink" >
                        <div className="prodImg">
                            <img src={this.props.ThumbnailImage} alt="temp" />
                        </div>
                        <div className="prodInfo">
                            <div className="prodName">
                                <p className="prodbrand">{this.props.title}</p>
                                <p className="prodDesc">{this.props.description}</p>
                            </div>
                            <div className="prodPrice">
                                <p className="final"><i><FontAwesomeIcon icon={faRupeeSign} /></i>{this.props.price}</p>
                                <p className="real"><i><FontAwesomeIcon icon={faRupeeSign} /></i>{this.props.originalPrice}</p>
                                <p className="discount">{this.props.discount}% off</p>
                            </div>
                            {/* <Addcartquantity /> */}
                        </div>
                    </Link>
                </div>
            </div>
        )
    }
}

export default Thumbnail;